import { BaseService } from "../baseService";
import * as storeUtils from "../../common/storeUtils";
import StorageClient from "../../clients/storageClient";
import ConfigClient from "../../clients/configClient";
import Logger from "../../clients/logger";
import { map as asyncMap, series as asyncSeries } from "async";
import FSBLDependencyManager from "../../common/dependencyManager";
import merge from "deepmerge";
ConfigClient.initialize();
StorageClient.initialize();
/** Daniel 12/21/2018
 * Previously, we relied on the BaseService's reference to the RouterClient
 * in this module, accessing `this.RouterClient` from within the StoreService.
 * Somehow this is broken with the new import syntax (literally, `this` is undefined
 * when you try to access `this.RouterClient`), so it's necessary to import the
 * RouterClient separately.
 * @TODO - Refactor StoreService to class that extends BaseService, and this issue
 * will go away.
*/
import RouterClient from "../../clients/routerClientInstance";

///We use global if no store is set
function StoreService() {
	var self = this;
	let persistingStores = {};
	var stores = {
		finsemble: {
			permissions: {
			},
			values: {

			}
		}
	};
	// Each store will be index here with an flattened object attached that hold the location of each object and it's parent.
	var mappings = {};
	window.mappings = mappings;
	this.stores = stores;
	window.stores = stores;
	this.setValue = function (params) {
		if (!params.field) { return Logger.system.error("no field provided", params); }
		if (!params.hasOwnProperty("value")) { return Logger.system.error("no value provided", params); }

		var topic = "valueChanged";

		if (!stores[params.store]) {
			stores[params.store] = { permissions: {}, values: {}, name: params.store };
		}

		var storeObject = stores[params.store];

		if (!storeObject.values[params.field]) { topic = "valueAdded"; }
		var removals = storeUtils.checkForObjectChange(storeObject.values, params.field, mappings[params.store]);
		// Update the values in the store
		storeUtils.setPath(storeObject.values, params.field, params.value);

		storeUtils.mapField(storeObject.values, params.field, mappings[params.store]);//remap
		if (removals) { publishRemovals(storeObject, removals); }
		if (storeObject.persist) StorageClient.save({ topic: "finsemble", key: ("distributedStore-" + storeObject.name), value: storeObject });
		publishObjectUpdates(storeObject, params.field, topic, mappings[params.store]);
	};

	// Publishes our updates to an object. This will crawl the tree backwards and send out notifications. `parent.child`-- If you changed the child value, anything listening on the parent channel will also ge the message.
	function publishObjectUpdates(store, startField, topic, mappings) {
		//always publish the
		RouterClient.publish("storeService" + store.store,
			{ topic: "storeChanged", store: store.name, storeData: store });
		var currentMapping = mappings;

		// Loop through the mapping for each item's parent.
		while (startField) {
			publishFieldUpdate(store, startField, topic, storeUtils.byString(store.values, startField));
			startField = currentMapping[startField];
		}
	}
	// Send out the actual notification
	function publishFieldUpdate(store, field, topic, value) {
		//Sends the message for each field
		RouterClient.publish("storeService" + store.name + "." + field,
			{ topic: topic, field: field, value: value, store: store.name });
	}

	//Send out our notification to anything listening on these channels. If you remove an object we need to make sure anything that was listening on that channel gets informed.
	function publishRemovals(store, removals) {
		for (var i = 0; i < removals.length; i++) {
			RouterClient.publish("storeService" + store.name + "." + removals[i],
				{ topic: "removal", field: removals[i], value: storeUtils.byString(store.values, removals[i]), store: store.name });
		}
	}

	// This will create a global store
	this.createStore = function (store, cb = Function.prototype) {
		if (!store.store) {
			return cb("no store provided:" + store);
		}
		if (!stores[store.store]) {
			mappings[store.store] = {};

			stores[store.store] = {
				store: store.store,
				name: store.store,
				persist: store.persist,
				global: true,
				//permissions: permissions,
				values: store.values ? store.values : {}
			};
			storeUtils.initObject(stores[store.store].values, null, mappings[store.store]);
			if (store.persist) {// save the store into storage
				persistingStores[store.store] = store.store;
				// This is just a reference to persisting stores;
				StorageClient.save({ topic: "finsemble", key: ("distributedStores"), value: persistingStores });
				// save the store. Having a separate record here makes saves much smaller.
				StorageClient.save({ topic: "finsemble", key: ("distributedStore-" + store.store), value: stores[store.store] });
			}
		}
		RouterClient.publish("storeService" + store, { topic: "storeCreated", store: stores[store.store] });
		return cb(null, stores[store.store]);
	};
	//Retrieves a store and passes it to the requestor.
	this.getStore = function (params, cb) {
		if (!params.store) { return cb("no store provided"); }
		if (!stores[params.store]) { return cb("store not found: " + params.store); }
		return cb(null, stores[params.store]);
	};
	//remove a store
	this.removeStore = function (params, cb) {
		if (params.store && stores[params.store]) {
			if (stores[params.store].persist) {
				delete persistingStores[params.store];// delete the store from our local list
				// remove the store from our list
				StorageClient.save({ topic: "finsemble", key: ("distributedStores"), value: persistingStores });
				// remove the store
				StorageClient.remove({ topic: "finsemble", key: ("distributedStore-" + params.store) });
			}

			delete stores[params.store];
			RouterClient.publish("storeService" + params.store, { topic: "storeDeleted", store: params.store });
			return cb(null, true);
		}
		return cb(null, false);
	};
	//Get a value out of the store
	this.getValue = function (params, cb) {
		var store = "finsemble";
		if (!params.field) { return cb("no field provided"); }

		if (params.store) { store = params.store; }
		if (!stores[store]) { return cb("store:" + store + " does not exist." + JSON.stringify(stores)); }
		var fieldValue = storeUtils.byString(stores[store].values, params.field);
		return cb(null, fieldValue);
	};
	//Get multiple values from a store
	this.getValues = function (params, cb) {
		var store = "finsemble";
		if (params.store) { store = params.store; }
		var storeObject = stores[store];
		if (!storeObject) { return cb("store:" + store + " does not exist." + JSON.stringify); }
		if (!params.fields) {
			return cb(null, storeObject.values);
		}

		var values = {};
		for (var i = 0; i < params.fields.length; i++) {
			var item = params.fields[i];
			var field = typeof item === "string" ? item : item.field;
			var fieldValue = storeUtils.byString(storeObject.values, field);
			values[field] = fieldValue;
		}
		return cb(null, values);
	};
	/**
	 *
	 * @param {*} params
	 * @param {String} params.field - allows you to do a merge at a lower level in the values object
	 * @param {Object} params.values - the values to merge into the store values
	 * @param {*} cb
	 */
	this.mergeValues = function (params, cb = Function.prototype) {
		// for now, this only works on load. Not sure how we want to handle publishing each level with a deep merge.
		if (!params.hasOwnProperty("values")) {
			Logger.system.error("no values provided", params);
			return cb();
		}

		if (!stores[params.store]) {
			stores[params.store] = { permissions: {}, values: {}, name: params.store };
		}

		var storeObject = stores[params.store];

		if (params.field) {
			storeObject.values[params.field] = merge(storeObject.values[params.field], params.values);
		} else {
			storeObject.values = merge(storeObject.values, params.values);
		}

		if (storeObject.persist) StorageClient.save({ topic: "finsemble", key: ("distributedStore-" + storeObject.name), value: storeObject });
		//publish out a store change
		this.RouterClient.publish("storeService" + storeObject.name, { topic: "storeChanged", store: storeObject.name, storeData: storeObject });
		return cb();
	};

	this.load = function (cb) {
		RouterClient.addPubSubResponder(/storeService*/, {});
		RouterClient.addResponder("storeService.getValue", function (err, message) {
			if (err) {
				return Logger.system.error(err);
			}
			return self.getValue(message.data, function (err, data) { message.sendQueryResponse(err, data); });
		});
		RouterClient.addResponder("storeService.getValues", function (err, message) {
			if (err) {
				return Logger.system.error(err);
			}
			return self.getValues(message.data, function (err, data) { message.sendQueryResponse(err, data); });
		});
		RouterClient.addResponder("storeService.createStore", function (err, message) {
			if (err) {
				return Logger.system.error(err);
			}
			return self.createStore(message.data, function (err, data) { message.sendQueryResponse(err, data); });
		});
		RouterClient.addResponder("storeService.setValue", function (err, message) {
			self.setValue(message.data);
			message.sendQueryResponse(err, message.data);
		});
		RouterClient.addResponder("storeService.getStore", function (err, message) {
			return self.getStore(message.data, function (err, data) { message.sendQueryResponse(err, data); });
		});
		RouterClient.addResponder("storeService.removeStore", function (err, message) {
			return self.removeStore(message.data, function (err, data) { message.sendQueryResponse(err, data); });
		});
		FSBLDependencyManager.onAuthorizationCompleted(() => {
			this.loadPersistingStores();
		});
		cb();
	};
	/**
	 * This loads stores from storage on start. It then checks configs for any new/edits to stores.
	 */
	this.loadPersistingStores = () => {
		let tasks = [
			(done) => {
				// we load all of our stores first

				StorageClient.get({ topic: "finsemble", key: "distributedStores" }, (err, data) => {
					if (err) return done(err);
					if (!data) return done();

					let storeKeys = Object.keys(data);
					asyncMap(storeKeys, (store, storeDone) => {
						StorageClient.get({ topic: "finsemble", key: ("distributedStore-" + store) }, (err, data) => {
							if (err) return storeDone(err);
							if (!data) {
								Logger.system.error("DISTRIBUTEDSTORESERVICE", "no store found:", store);
								return storeDone("no store found");
							}
							this.createStore(data, storeDone);
						});
					}, (err) => {
						done(err);
					});
				});
			},
			(done) => {
				// load stores from the config
				ConfigClient.getValue({ field: "finsemble.servicesConfig.distributedStore" }, (err, data) => {
					if (err) return done(err);
					if (!data) {
						// the config isn't setup
						Logger.system.log("DISTRIBUTEDSTORESERVICE", "servicesConfig.distributedStore does not exist");
						return done();
					}
					Logger.system.log("DISTRIBUTEDSTORESERVICE", "serviceConfig", data);
					// no initial stores
					if (!data.initialStores || !data.initialStores.length) {
						Logger.system.log("DISTRIBUTEDSTORESERVICE", "No initialStores found");
						return done();
					}
					asyncMap(data.initialStores, (store, storeDone) => {
						if (stores[store.name]) {
							Logger.system.log("DISTRIBUTEDSTORESERVICE", "loading foundation for", store);
							// if we don't have a foundation config just move on.
							if (!store.foundation) return storeDone();
							return this.mergeValues({ store: store.name, values: store.foundation }, storeDone);
						}
						Logger.system.log("DISTRIBUTEDSTORESERVICE", "creating a new store", store);
						this.createStore({
							persist: true,
							global: true,
							store: store.name,
							values: store.default
						}, () => {
							if (!store.foundation) return storeDone();
							return this.mergeValues({ store: store.name, values: store.foundation }, storeDone);
						});

					}, (err) => {
						done(err);
					});
				});

			}
		];

		asyncSeries(tasks, (err) => {
			if (err) {
				return Logger.system.error("Data Store persist store load Error:", err);
			}
			Logger.system.log("Data store loaded from all persistent stores");
		});
	};
}

StoreService.prototype = new BaseService({
	name: "dataStoreService",
	shutdownDependencies: {
		services: ["windowService"]
	}
});
var serviceInstance = new StoreService();
Logger.start();
serviceInstance.onBaseServiceReady(function (callback) {
	serviceInstance.load(callback);
});

serviceInstance.start();
window.RouterClient = RouterClient;
window.StoreService = serviceInstance;
export default serviceInstance;


